import { CetusTooltip } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { cancelBubble } from '@cetus/utils'
import { Center, HStack, Image, StackProps, Text } from '@chakra-ui/react'
import { ReactNode } from 'react'
import { useNavigate } from 'react-router-dom'

interface FarmingIcon extends StackProps {
  tooltip?: ReactNode
}

const FarmingIcon = ({ tooltip, ...rest }: FarmingIcon) => {
  const navigate = useNavigate()
  const { isApp } = useWindowWidth()
  return (
    <HStack
      w="20px"
      h="20px"
      onClick={e => {
        cancelBubble(e)
        isApp ? '' : navigate('/farms')
      }}
      {...rest}
    >
      <CetusTooltip placement="top" tooltip={tooltip ? tooltip : <Text fontSize="12px">Farm rewards available</Text>}>
        <Center>
          <Image w="100%" h="100%" minW="100%" src="/images/icon_farms.png" />
        </Center>
      </CetusTooltip>
    </HStack>
  )
}

export default FarmingIcon
